import React from 'react';
import logo from '../assets/logo/logoFeuille.png';
import '../styles/Banner.css';
import Recommendation from './Recommendation';


function Banner() {
	const title = 'La boutique Jungle'
	return (
		
		<div>
			<div className="lmj-banner">
			<img src={logo} alt='Logo boutique jungle' className='lmj-logo' />
			<h1 className='lmj-title'>{title}</h1>
			</div>
			<div className="recommandation"><Recommendation /></div>
		</div>
		
	)
}

export default Banner
