import React from 'react';
import CareScale from './CareScale';
import '../styles/PlantItem.css';


function PlantItem({ id, name, isBestSale, cover, desc, light, water }) {

	function handleClick(plantName) {
		alert(`Vous voulez acheter un ${plantName}? Tr√®s bon choix üå±‚ú®`)
	}

	return (
		<div className="grid-cards-section">
			<li key={id} className='lmj-plant-item' >
				
				<img className='lmj-plant-item-cover' src={cover} alt={`${name} cover`} onClick={() => handleClick(name)} />
				<div className="top-mention">
					{isBestSale === true ? <span className="promo-text">Top</span> : null}
				</div>
				<p className="item-title">{name}</p>
				<p className="item-desc">{desc}</p>
				<div className="water">
					<CareScale careType='water' scaleValue={water} />
				</div>
					<CareScale careType='light' scaleValue={light} />
				<div>
				</div>
			</li>
		</div>
	)
}

export default PlantItem
