import { useState, useEffect } from 'react';
import '../styles/Cart.css';

function Cart({ cart, updateCart }) {

	const [isOpen, setIsOpen] = useState(false)
	// total cart
	const total = cart.reduce(
		(acc, plantType) => acc + plantType.amount * plantType.price,
		0
	)

	const deleteItem = (index) => {
		const newCart = [...cart]
		newCart.splice(index, 1)
		updateCart(newCart)
	}

	function addItem(name, price) {
		const currentPlantSaved = cart.find((plant) => plant.name === name)
		const cartOtherPlants = cart.filter(
			(plant) => plant.name !== name
		)
	
		updateCart(() => [
			...cartOtherPlants,
			{ name, price, amount: currentPlantSaved.amount + 1 }
		] )
	}

	function removeItem(name, price){
		const currentPlantSaved = cart.find((plant) => plant.name === name)
		const cartOtherPlants = cart.filter(
			(plant) => plant.name !== name
		)
		function testPositive(cartOtherPlants,currentPlantSaved ) {
			
			if (currentPlantSaved.amount > 1) {
				updateCart(() => [
					...cartOtherPlants,
					{ name, price, amount: currentPlantSaved.amount - 1 }
				] )
			} else {
			}
		  }
		  testPositive(cartOtherPlants,currentPlantSaved);
	}
	
	useEffect(() => {
		document.title = `${total}` > 0 ? `La Boutique Jungle: ${total}‚Ç¨ d'achats` : `La Boutique Jungle`
	})

	return isOpen ? (
		<div className='lmj-cart'>
			<button
				className='lmj-cart-toggle-button-close'
				onClick={() => setIsOpen(false)}
			>
				<i className="fas fa-times"></i>
			</button>
			<h2>Panier</h2>
			{cart.map(({ name, price, amount }, index) => (
				<div className="cart-row" key={`${name}-${index}`}>
					 <i className="far fa-trash-alt" onClick={() => deleteItem(index)}></i> <i className="far fa-minus-square" onClick={() => removeItem(name, price)}></i> {amount} <i className="far fa-plus-square" onClick={() => addItem(name, price)}></i> {name} {price}‚Ç¨
				</div>
			))}
			<h3>Sous-total : {total}‚Ç¨</h3>
			<button className="btn-cart" onClick={() => updateCart([])}> vider le panier</button>
		</div>
	) : (
		<div className='lmj-cart-closed'>
			<button
			id="cart"
				className='lmj-cart-toggle-button-cart'
				onClick={() => setIsOpen(true)}
			>
				<i className="fas fa-shopping-cart"></i>
			</button>
		</div>
	)
}


export default Cart
