import React from 'react';
import '../styles/CareScale.css'
import Sun from '../assets/icons/sun.svg'
import Water from '../assets/icons/water.svg'


const quantityCare = {
    1: 'peu',
    2: 'mod√©r√©ment',
    3: 'beaucoup'
}


function CareScale({scaleValue, careType}) {
    const range = [1, 2, 3]

    const scaleType =
		careType === 'light' ? (
			<img src={Sun} alt='sun-icon' />
		) : (
			<img src={Water} className="water" alt='water-icon' />
		)


    return (
        <div className="care-range-icons" onClick={() => alert(`Cette plante a besoin de ${quantityCare[scaleValue]} ${careType === 'light' ? 'de lumi√®re' : 'd\'eau'}`)}>
            {range.map((rangeElem) =>
                scaleValue >= rangeElem ? <span key={rangeElem.toString()} >{scaleType}</span> : null
            )}
            
        </div>
    )
}

export default CareScale
